@keyframes leftToRightSlideSM {
  0% {
    transform: translate(-100%, 0%);
    z-index: 2;
  }
  100% {
    transform: translate(0%, 0%);
    z-index: 2;
  }
}

@keyframes rightToLeftSlideSM {
  0% {
    transform: translate(100%, 0%);
  }
  100% {
    transform: translate(0%, 0%);
  }
}

.text-thumbnail {
  animation: rightToLeftSlideSM;
  animation-duration: 1s;
  padding-right: 50px;
  padding-top: 50px;
  padding-bottom: 50px;
  padding-left: 150px;
  margin-left: -100px;
  z-index: -1;
}
.text-thumbnail h3 {
  font-size: 1.5vw;
}
.custom-thumbnail:first-child img {
  animation: leftToRightSlideSM;
  animation-duration: 1s;
}
.custom-thumbnail {
  display: flex;
  flex-direction: row;
}

@media screen and (max-width: 768px) {
  .custom-thumbnail {
    flex-direction: column;
  }

  .text-thumbnail {
    padding-right: 50px;
    padding-top: 50px;
    padding-bottom: 50px;
    padding-left: 50px;
  }
}

.fade-in-element {
  transition-delay: 0.5s;
  transition: 0.7s;
}
.fade-in-element.is-visible {
  opacity: 1;
  transform: translateY(0px) translateZ(0);
}

.from-bottom {
  transform: translateY(70px) translateZ(0);
}

.from-top {
  transform: translateY(-200px) translateZ(0);
}
.arrow {
  height: 17vh;
}
.arrow:hover {
  opacity: 70%;
  cursor: pointer;
}
