@font-face {
  font-family: MadeTommy;
  src: url("/public/ressources/made-tommy-medium-personal-use.otf");
}

.bg-green {
  background-color: #033018;
}

.navbar-icon {
  height: 35px;
}

.thumbnail:hover {
  transition: 0.3s;
  transform: scale(1.05, 1.05);
}

.custom-link {
  color: white;
}

.custom-link:hover {
  cursor: pointer;
  opacity: 70%;
}

.bg-blue {
  background-color: #071f28;
  /*background-color: #0a111b;*/
}

.bg-dark-blue {
  background-color: #06181f;
}

#logo {
  aspect-ratio: 4;
}

.blur {
  backdrop-filter: brightness(30%);
}

@media screen and (max-width: 768px) {
  #first-view {
    height: 70vh;
    background-position: center top;
  }
}

#first-view {
  /*background: -webkit-linear-gradient(360deg, #05946f,#008a76,#007f79,#007378,#116873);                       background: linear-gradient(360deg, #05946f,#008a76,#007f79,#007378,#116873);/* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */
  background-image: url("/public/groupe1/local-sombre.jpg");
  background-size: cover;
  background-position: 25% 75%;
}

body {
  background-image: url("/public/ressources/back-2.png");
  font-family: MadeTommy, sans-serif;
}

.shadow-black {
  box-shadow: 30px 30px 30px #141414;
}

.bg-feuille {
  background-image: url("/public/groupe1/mur-feuille.jpg");
  background-size: cover;
  background-position: 25% 50%;
}

opacity-h-50:hover {
  opacity: 0.5;
}

.text-green {
  color: #01ff01;
}
